//工具集

/**
 * [matrixToolkit 矩阵和数组相关的工具]
 * @type {Object}
 */
const matrixToolkit = {
	makeRow(v = 0) {
		const array = new Array(9);
		array.fill(v);
		return array;
	},

	makeMatrix(v = 0) {
		return Array.from({ length: 9 }, () => this.makeRow(v));
	},


	/**
	 * Fisher-Yates 洗牌算法：指针从数组的第一项开始，
	 * 与从该项起到最后一项中的任意一个数交换位置，
	 * 剩余的数保持不变，同时指针后移一位，
	 * 直到指针指向数组的最后一项
	 * @param  {array} array
	 * @return {array} array
	 */
	shuffle(array) {
		const endIndex = array.length - 2;
		for(let i = 0;i <= endIndex; i++) {
			const j = i + Math.floor(Math.random() * (array.length - i));
			[array[i], array[j]] = [array[j], array[i]];
		}
		return array;
	},

	/**
	 * 检查指定位置可以填写数字 n
	 */
	checkFillable(matrix, n, rowIndex, colIndex) {
		const row = matrix[rowIndex];
		const column = this.makeRow().map((v,i) => matrix[i][colIndex]);
		const { boxIndex } = boxTookit.convertToBoxIndex(rowIndex, colIndex); //宫的位置
		const box = boxTookit.getBoxCells(matrix, boxIndex);
		for(let i=0;i<9;i++) {
			if(row[i] === n || column[i] === n || box[i]===n) {
				return false;
			}
		}
		return true;
	}
};

/**
 * 宫坐标系工具
 */
const boxTookit = {
	
	/**
	 * 得到每个宫中的数字
	 * @param  {array} matrix   矩阵
	 * @param  {number} boxIndex 宫号
	 */
	getBoxCells(matrix, boxIndex) {
		const startRowIndex = Math.floor(boxIndex / 3) * 3;
		const startColIndex = boxIndex % 3 * 3;
		const result = [];
		for(let cellIndex = 0; cellIndex < 9; cellIndex++) {
			const rowIndex = startRowIndex + Math.floor( cellIndex / 3);
			const colIndex = startColIndex + cellIndex % 3;
			result.push(matrix[rowIndex][colIndex]);
		}
		return result;
	},


	/**
	 * 将行列号转换为宫号和格号
	 * @param  {number} rowIndex 行号
	 * @param  {number} colIndex 列号
	 */
	convertToBoxIndex(rowIndex, colIndex) {
		return {
			boxIndex: Math.floor(rowIndex / 3) * 3 + Math.floor(colIndex / 3), //宫的序号
			cellIndex: rowIndex % 3 * 3 + colIndex % 3 //宫中格的序号
		}
	},
	
	/**
	 * 将宫号和格号转换为行列号
	 * @param  {number} boxIndex  宫号
	 * @param  {number} cellIndex 格号
	 */
	convertFromBoxIndex(boxIndex, cellIndex) {
		return {
			rowIndex: Math.floor(boxIndex / 3) * 3 + Math.floor(cellIndex / 3), //行号
			colIndex: boxIndex % 3 * 3 + cellIndex % 3  //列号
		}
	}
}

//工具集

module.exports = class Toolkit {

	/**
	 * 矩阵和数组相关的工具
	 */
	static get matrix() {
		return matrixToolkit;
	}

	/**
	 * 宫坐标系相关的工具
	 */
	static get box() {
		return boxTookit;
	}
};


