//处理弹出的操作面板
module.exports = class PopupNumbers {
	constructor($panel) {
		this._$panel = $panel.hide().removeClass("hidden");

		this._$panel.on("click", "span", e => {
			const $cell = this._$targetCell;
			const $span = $(e.target);
			
			//mark1，mark2 回填样式
			if($span.hasClass("mark1")) {
				//回填样式
				if($cell.hasClass("mark1")) {
					$cell.removeClass("mark1");
				} else {
					$cell.removeClass("mark2")
						.addClass("mark1");
				}
			} else if($span.hasClass("mark2")) {
				//回填样式
				if($cell.hasClass("mark2")) {
					$cell.removeClass("mark2");
				} else {
					$cell.removeClass("mark1")
						.addClass("mark2");
				}
			} else if($span.hasClass("empty")) {
				//取消数字和mark
				$cell.text(0).addClass("empty").removeClass("mark1 mark2");
			} else {
				//1-9回填数字
				$cell.removeClass("empty").text($span.text());
			}
			this.hide();
		});
	}

	popup($cell) {
		this._$targetCell = $cell;
		const { left, top, right } = $cell.position();
		const _$body = $("body").width();
		const _$width = this._$panel.width();
		if(left + _$width > _$body) {
			this._$panel.css({
				left: `${left-_$width+this._$targetCell.width()}px`,
				top: `${top}px`
			}).show();
		} else {
			this._$panel.css({
				left: `${left}px`,
				top: `${top}px`
			}).show();
		}
		
	}

	hide() {
		this._$panel.hide();
	}
}


